import {fastAPI, getUserType} from "../api/api"

export const showUsersAC = (users: Array<getUserType>) => {
    debugger
    return {
        type: 'TEST/showUsers',
        users
    } as const
}

export type showUsersAT = ReturnType<typeof showUsersAC>


export type actionType = showUsersAT

/*export type usersType = {
    id: number,
}*/

type InitStateType = {
    users: Array<getUserType>,
}

export const initState: InitStateType = {
    users: [],
}

export const zenReducer = (state: InitStateType = initState, action: actionType): InitStateType => {
    switch (action.type) {
        case 'TEST/showUsers': {
            return {...state, users: action.users}
        }
        default: {
            return state
        }
    }
}

//thunk

export const showUsersTC = () => {
    return (dispatch: any) => {
        fastAPI.showUsers()
            .then(res => {
                debugger
                dispatch(showUsersAC(res.data))
            })
            .catch(e => {
                debugger
                alert('error')
            })
    }
}

